h1 {
  color: green;
  font-style: italic;
}

.note {
  color: grey;
  padding-top: 3px;
  font-size: 15px;
}

.error {
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

button {
  background: lightgrey;
  border-style: solid;
  border-radius: 5px;
  padding: 5px 10px;
  margin: 5px 10px;
}

input {
  padding: 5px 10px;
  margin: 5px 0;
  display: inline-block;
  border-radius: 5px;
  border-style: solid;
  border-width: 1px;
  border-color: grey;
}
